import { getSearch } from '@/utils'
export default {
  created () {
    this.reportTime()
  },
  beforeDestroy () {
    clearTimeout(this.timer)
  },
  methods: {
    reportTime () {
      clearTimeout(this.timer)
      const query = getSearch()
      this.timer = setTimeout(() => {
        this.$apis.userOperator.reportTime(query)
      }, 8000)
    }
  }
}
