    <!-- <img
      src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/BdCheck/btn1.png"
      alt=""
      @click="submit"
      class="btnBottom" /> -->
    <VanDialog
      v-model="showDialog"
      :close-on-click-overlay="true"
      :show-confirm-button="false">
      <div class="tips">
        <img
          src="https://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/BdCheck/qrcode1.png"
          alt=""
          class="qr" />
        <img
          :src="qrcode"
          alt=""
          class="qrcode" />
      </div>
    </VanDialog>
  </div>
</template>
<script>
import CountDown from '@/components/common/CountDown'
import { getSearch } from '@/utils'
import { wmAppid } from '@/config'
import { codeMap } from './codeMap'
import reportTime from './report.mixin'
export default {
  components: {
    CountDown
  },
  mixins: [reportTime],
  data () {
    return {
      showDialog: false,
      qrcode: '',
      form: {
        phone: '',
        code: ''
      }
    }
  },
  methods: {
    onSubmit (e) {
      console.log('验证通过', e)
      const query = getSearch()
      this.$apis.userOperator.getLastOrderInfo({
        ...e,
        ...query
      })
        .then(res => {
          this.qrcode = res.qrcode
          this.showDialog = true
        })
        .catch(err => {
          this.showMsg(err)
        })
    },
    submit () {
      this.$refs.form.submit()
    },
    async goMini () {
      const path = 'pages/index/index'
      const phone = this.form.phone
      const query = { ...getSearch(), phone }
      const { url } = await this.$apis.userOperator.getMiniHref({
        path,
        appid: wmAppid,
        query
      })
      window.location.href = url
    },
    showMsg (err) {
      console.log(codeMap)
      const { code, msg } = err
      if (code === 30006) {
        console.log('小程序分班')
        // 未分别 去小程序
        this.$dialog({
          message: msg,
          confirmButtonText: '去分班',
          cancelButtonText: '返回',
          showCancelButton: true
        }).then(res => {
          this.goMini()
        })
        return
      }
      if (code === 30000) {
        // 未购买
        this.$dialog({
          message: msg,
          confirmButtonText: '去购买',
          cancelButtonText: '返回',
          showCancelButton: true
        }).then(res => {
          location.href = 'https://tf.weimiao.cn/?id=429&typeId=1&teamId=32&medId=1210&demId=1&dicId=1&planId=1&push_id=C1000&account=210702395#/landPage?from=wm'
        })
        return
      }
      if (codeMap[code]) {
        this.$dialog({
          message: codeMap[code].msg
        })
      }
    },
    beforeSend (start) {
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          this.showMsg({ code: err.message })
        })
    },
    showErrorMsg (err) {
      const code = err.errors[0].message
      this.showMsg({ code })
    }
  }
}
</script>
