      .submit {
        width: 455px;
        height: 63px;
        background: #f12c30;
        border-radius: 31px;
        font-size: 30px;
        font-family: Source Han Sans CN;
        font-weight: bold;
        color: #ffffff;
        line-height: 63px;
      }
    }
    /deep/ .van-cell {
      &.phonecode {
        width: 244px;
      }
      margin-bottom: 20px;
      padding-top: 0;
      background: #fff;
      font-family: SourceHanSansSC;
      font-weight: 500;
      line-height: 55px;
      color: #999;
      height: 58px;
      border: 1px solid #d4d4d4;
      background: linear-gradient(0deg, #ffffff 0%, #ffffff 100%);
      opacity: 0.5;
      border-radius: 29px;
      &::after {
        display: none;
      }
    }
  }
  .btnBottom {
    width: 630px;
    position: fixed;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
  }
}
</style>
